.drink-info {
    max-width: 800px;
    margin: 0 auto 0 auto;
}

.drink-info img {
    margin: 0 auto 0 auto;
    border-radius: 4px;
    width: 100%;
    aspect-ratio: 1/1;
}

.ingredients {
    padding: 1px;
    border-radius: 4px;
}

.ingredients h3 {
    font-weight: normal;
}

.ingredients p {
    margin: 0 1rem 0 1rem;
    font-size: 12pt;
}

ul {
    list-style-type: none;
    padding: 0 1rem;
    margin-bottom: 1rem;
}

.ingredients li:nth-child(1) {
    border-top: 1px solid lightgray;
}

.ingredients li {
    padding: 0.75rem;
    padding-left: 1rem;
    border-bottom: 1px solid lightgray;
}

.tag-list {
    margin: 1rem 0 0;
    display: flex;
    flex-Wrap: wrap;
    gap: 0.5rem;
    justify-Content: center;
}

.extra-info {
    display: flex;
    flex-wrap: wrap;
    margin-top: 2rem;
    padding-top: 2rem;
    border-top: 1px solid lightgray ;
    gap: 0.5rem;

}

.image-loading{
    animation: pulse 0.75s infinite alternate-reverse;
}

@media screen and (min-width: 375px) {
    .drink-info img {
        max-width: 75%;
    }
}

@media screen and (min-width: 500px) {
    .drink-info img {
        max-width: 50%;
    }
}

@keyframes pulse {
    from {
        background-color: transparent;
    }
    
    to {
        background-color: lightgray;
    }
}